<template>
  <div class="search">
    <!-- 搜索框 -->
    <div class="searchBar clearfix">
      <div class="searchBar_lr fl" @click="goPage('Category')">
        <van-icon size="23px" name="arrow-left" />
      </div>
      <div class="searchBar_bar fl">
        <van-search placeholder="请搜索商品" v-model="keywords" @search="searchs" />
      </div>
      <div class="searchBar_lr fl">
        <van-icon size="23px" name="qr" />
      </div>
    </div>

    <!-- 商品列表 -->
    <div class="context">
      <div
        class="list clearfix"
        v-for="(item,index) in productlist"
        @click="goDetail(index)"
        :key="index"
      >
        <div class="list_img fl">
          <!-- 如果图片没有数据，则显示空状态 -->
          <img
            v-if="item.goods_big_logo == 0"
            class="auto-img"
            :src="require('../assets/images/kzt.png')"
            alt
          />
          <img v-else class="auto-img" v-lazy="item.goods_big_logo" alt />
        </div>
        <div class="list_con fl">
          <div class="list_name">{{item.goods_name}}</div>
          <div class="list_specifications">重{{item.goods_weight}}kg</div>
          <div class="list_price" v-if="item.goods_price == 0">￥9999</div>
          <div class="list_price" v-else>￥{{item.goods_price}}</div>

          <div class="list_assess">200+条评论 好评率99%</div>
          <div class="list_assess">小鹅官方旗舰店 进店></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //商品列表
      productlist: [],
      // 截取路由参数
      keywords: "",
    };
  },
  created() {
    // 截取路由参数
    this.keywords = this.$route.query.keywords;
    // 
    //获取商品列表数据
    this.Productlist();
  },
  methods: {
    //跳转页面
    goPage(name) {
      this.$router.push({ name });
    },

    //获取商品列表数据
    Productlist() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        methods: "GET",
        url: "/goods/search",
        params: {
          query: this.keywords,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.meta.status == 200) {
            this.productlist = result.data.message.goods;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    //搜索
    searchs() {
      this.$router.push({
        path: this.$route.path,
        query: Object.assign({}, this.$route.query, {
          keywords: this.keywords,
        }),
      });
      this.Productlist();
    },
    // 获取商品id
    goDetail(index) {
      let goods_id = this.productlist[index].goods_id;
      let keywords = this.productlist;
      this.$router.push({ name: "Detail", query: { goods_id, keywords } });
    },
  },
};
</script>

<style lang="less" scoped>
.search {
  // 搜索框
  .searchBar {
    width: 100%;
    height: 100%;
    .searchBar_lr {
      width: 10%;
      line-height: 70px;
      text-align: center;
      color: rgb(122, 127, 132);
    }
    .searchBar_bar {
      width: 80%;
    }
  }
  // 商品列表
  .context {
    width: 100%;
    height: calc(~"100% - 70px");
    .list {
      width: 90%;
      margin: 2.5%;
      padding: 2.5%;
      height: 100%;
      .list_img {
        width: 120px;
        height: 120px;
      }
      .list_con {
        width: calc(~"90% - 110px");
        margin-left: 5%;
        padding-bottom: 5px;
        border-bottom: 1px solid #ccc;
        .list_name {
          height: 35px;
          font-size: 13px;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        .list_specifications {
          height: 20px;
          color: #999999;
          margin: 10px 0;
        }
        .list_price {
          font-size: 20px;
          color: #eb5211;
        }
        .list_assess {
          padding-top: 5px;
          color: #999999;
          font-size: 12px;
        }
      }
    }
  }
}
</style>